@import "../components/variables";
@import "../components/mixin";

body{
  .error{
    width: 100%;
    padding-top: 25px;
    .containerParent();
    .container{
      .containerChild();
      margin-bottom: 100px;
      display: grid;
      grid-template-areas: "breadcrumb"
      "contacts";
      grid-template-columns: 100%;
      .content__error__breadcrumb {
        grid-area: breadcrumb;
        ul {
          display: flex;
          li {
            a {
              font: .875em/1em @sansregular;
              color: #474747;
            }
            span {
              font: .875em/1em @sansregular;
              color: #474747;
            }
          }
        }
      }
      .content__error{
        grid-area: contacts;
        h5 {
          font: 2.25em/2.375em @serifitalic;
          color: @linkColor;
        }
        ul{
          width: 100%;
          display: flex;
          flex-direction: column;
          align-items: center;
          li{
            &:nth-child(1){
              margin: 40px 0;
              width: 100%;
              display: flex;
              align-items: center;
              p{
                width: 50%;
                &:nth-child(1){
                  text-align: end;
                  img{
                    margin: 0 20px 0 0;
                    width: 70%;
                  }
                }
                &:nth-child(2){
                  font: 12.5em/.25em @sansbold;
                  color: @mainColor;
                  span{
                    font-size: .1em;
                    line-height: .125em;
                  }
                }
              }
            }
            &:nth-child(2){
              margin: 10px 0;
              p{
                font: 1em/1.125em @sansregular;
                color: #474747;
                a{
                  color: @mainColor;
                  text-decoration: underline;
                }
              }
            }
            &:nth-child(3){
              margin: 40px 0;
              a{
                width: 250px;
                display: block;
                font: 1em/40px @sansregular;
                color: #fff;
                text-align: center;
                text-transform: uppercase;
                text-decoration: none;
                background-color: @mainColor;
                border-radius: 5px;
              }
            }
          }
        }
      }
    }
  }
}